import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { SelectAjax } from 'components/common/index';
// import './style.scss';

@Form.create({ name: 'FormConfigValues' })
class FormConfigValues extends Component {
  state = {};

  getReceipts = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
          
        },
        {
          id: '2',
          label: 'B',
          
        },
        {
          id: '3',
          label: 'C',
          
        },
  
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
    data,
    };
    return res; 
  }

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={4}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="Hc k">
              {getFieldDecorator('semesterId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>  
          </Col>
          <Col span={6}>
            <Form.Item label="Khon thu">
              {getFieldDecorator('receipt', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={this.getReceipts}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label="% gim">
              {getFieldDecorator('reducePercent', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="S tin gim">
              {getFieldDecorator('reduceMoney', {})(
                <Input />
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormConfigValues;



